import { FC, FormEvent } from 'react';
import {
  Button,
  Input,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { useSelector, useDispatch } from "../../services/types/index";
import { Link, Redirect, useLocation } from "react-router-dom";
import { useForm } from "../../hooks/use-form";
import { resetPassword } from "../../services/actions/auth";
import styles from "./reset-password.module.css";
import { TLocation } from '../../services/types/data';

export const ResetPassword: FC = () => {
  const dispatch = useDispatch();
  const location = useLocation<TLocation>();

  const { resetPasswordSuccess, recoveryPasswordSuccess } = useSelector(
    (state) => state.user
  );

  const { values, handleValues } = useForm({ password: "", code: "" });

  function formSubmit(e: FormEvent<HTMLFormElement>) {
    e.preventDefault();
    dispatch(resetPassword(values.password, values.code));
  }

  if (!recoveryPasswordSuccess) {
    return <Redirect to={{ pathname: "/forgot-password" }} />;
  }

  return (
    <div className={styles.container}>
      <h2 className="pb-6 text_type_main-medium text"> </h2>

      <form className={styles.form} onSubmit={formSubmit}>
        <div className="pb-6">
          <PasswordInput
            onChange={handleValues}
            placeholder={"  "}
            value={values.password}
            name={"password"}
            size="default"
          />
        </div>
        <div className="pb-6">
          <Input
            onChange={handleValues}
            type={"text"}
            placeholder={"   "}
            value={values.code}
            name={"code"}
            error={false}
            errorText={""}
            size={"default"}
          />
        </div>
        <div className={styles.button}>
          <Button
            disabled={!values.password || !values.code}
            type="primary"
            size="medium"
            htmlType="submit"
          >
            {!!resetPasswordSuccess ? (
              <Redirect to={location.state?.from || "/profile"} />
            ) : (
              ""
            )}
            
          </Button>
        </div>
      </form>

      <p className="pt-20 text_type_main-default text_color_inactive text">
         ?
        <Link className={styles.link} to="/login">
          
        </Link>
      </p>
    </div>
  );
};
